import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'programa-dados',   templateUrl: 'programa_dados.component.html' }) export class ProgramaDadosComponent implements OnInit {       displayMode: ProgramaDadosDisplayModeEnum;     displayModeEnum = ProgramaDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = ProgramaDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = ProgramaDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = ProgramaDadosDisplayModeEnum.Edit;           break;       }     }  }  enum ProgramaDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


