import { Routes, RouterModule } from '@angular/router';  import { ProgramaDadosComponent }   from './programa_dados.component'; import { ProgramaDadosDetailsComponent } from './programa_dadosDetails.component'; import { ProgramaDadosEditComponent } from './programa_dadosEdit.component';  const programa_dados_routes: Routes = [   {     path: '',     component: ProgramaDadosComponent,     children: [       { path:'details', component: ProgramaDadosDetailsComponent },       { path:'edit', component: ProgramaDadosEditComponent }     ]   } ];  export const programa_dados_routing = RouterModule.forChild(programa_dados_routes); 


